import { useState } from 'react'
import {useDispatch, useSelector} from "react-redux"
import { DragDrop } from '@uppy/react'
import Uppy from '@uppy/core'
import '@uppy/core/dist/style.css'
import NumberInput from '@components/number-input'
import thumbnailGenerator from '@uppy/thumbnail-generator'
import "./styles.scss"
import './styles copy.scss'
import '@styles/react/libs/file-uploader/file-uploader.scss'
import { Card, CardHeader, CardTitle, CardBody, Row, Col, Button, Input, Label, FormGroup} from 'reactstrap'
// import { addfile} from "../../store/actions"
// import Table from  "./list/Table"

const SelectReact = () => {
    const store = useSelector((state) => state.corporateteams)
    const [data, setData] = useState(null)
    const [img, setImg] = useState(null)
    const [imageError] = useState(false)
    const dispatch = useDispatch()
    const [deliver, setDeliver] = useState({nam: '', quantity: '', image:''})

    const uppy = new Uppy({
      meta: { type: 'avatar' },
      restrictions: { maxNumberOfFiles: 1 },
      autoProceed: true
    })
  
    uppy.use(thumbnailGenerator)
    
    uppy.on('file-added', (file, preview) => {
      console.log(file.data.lastModified)
      console.log(file.data)
      console.log(uppy.store.state.files[0])
      console.log(file)
      console.log(preview)
      setImg(preview)
      setData(file.data)
    })

    const submit = (e) => {
      e.preventDefault()
      const param = new FormData()
      param.append('file', data)
      console.log(param)
      console.log(deliver)
      dispatch()
    }

  return (
    <Card>
      <CardHeader>
        <CardTitle tag='h4'>{store.isloading }</CardTitle> 
      </CardHeader>
      <CardBody>      
        <Col>
            <FormGroup>
              <Label for="nameVertical">Caption</Label>
              <Input
                type="text"
                name="caption"
                id="caption"
                // innerRef={register({ required: true })}
                // invalid={errors.caption && true}
                // value={prizeform.caption}
                // onChange={(e) => setPrizeform({ ...prizeform, caption: e.target.value })}
                onChange={(e) => setDeliver({...deliver, nam: e.target.value})}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>  
          <Col>
            <FormGroup>
              <Label for="nameVertical">Series</Label>
              <Input
                type="text"
                name="caption"
                id="caption"
                // innerRef={register({ required: true })}
                // invalid={errors.caption && true}
                // value={prizeform.caption}
                // onChange={(e) => setPrizeform({ ...prizeform, caption: e.target.value })}
                onChange={(e) => setDeliver({...deliver, nam: e.target.value})}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>  
          <Col>
            <FormGroup>
              <Label for="nameVertical">Fixture</Label>
              <Input
                type="text"
                name="caption"
                id="caption"
                // innerRef={register({ required: true })}
                // invalid={errors.caption && true}
                // value={prizeform.caption}
                // onChange={(e) => setPrizeform({ ...prizeform, caption: e.target.value })}
                onChange={(e) => setDeliver({...deliver, nam: e.target.value})}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>  
          <Col>
          <FormGroup>
          <NumberInput 
          onChange= {(e) => setDeliver({...deliver, quantity: e}) }
          style={{ width: '400px' }} />
        </FormGroup>
          </Col>
        <Row className="wrapper-add" >
        <Col className="add-post-edit">
         <div className={`mt-2 ${imageError ? 'img-error-post' : ''}`}>
         <DragDrop uppy={uppy}
           locale={{
            strings: {
              // Text to show on the droppable area.
              // `%{browse}` is replaced with a link that opens the system file selection dialog.
              dropHereOr: 'Drop file or %{browse}',
              // Used as the label for the link that opens the system file selection dialog.
              browse: 'browse'
            }
          }}
         />
        {img !== null ? <img className='rounded mt-2' src={img} alt='avatar' /> : null}
                {img && (
                  <div className="uploaded-files">
                    <img src={img} className="post-preview" />
                  </div>
                )}
              </div>
              </Col>
             </Row>
             <div className="mt-3">
        <Button.Ripple 
        color='primary'
        onClick={(e) => submit(e)}
        >submit</Button.Ripple>
        </div>                  
      </CardBody>
    </Card>
  )
}
export default SelectReact